<template>
  <div>
    <Mheader>添加</Mheader>
    <ul class="contains">
      <li>
        <label for="bookname">图书名称</label>
        <input type="text" v-model="book.bookName" name="bookname">
      </li>
      <li>
        <label for="bookinfo">图书介绍</label>
        <input type="text" v-model="book.bookInfo" name="bookinfo">
      </li>
      <li>
        <label for="bookprice">图书价格</label>
        <input type="text" v-model.number="book.bookPrice" name="bookprice">
      </li>
      <li>
        <label for="bookCover">图书地址</label>
        <input type="text" v-model="book.bookCover" name="bookCover">
      </li>
      <button @click="add">添加</button>
    </ul>
  </div>
</template>
<script>
  import Mheader from '../base/Mheader.vue'
  import {addBook} from '../api'
  export default {
    data(){
      return {
        book:{}
      }
    },
    created(){},
    methods:{
      async add(){
        await addBook(this.book);
        this.$router.push('/list');
      }
    },
    computed:{},
    components:{Mheader}
  }
</script>
<style scoped lang="less">
  .contains{
    margin-top: 40px;
    padding:30px;
  li{

    margin-bottom: 20px;
  };
  label{
    display: block;
    font-size: 20px;
    margin-bottom: 10px;
  }
  input{
    display: block;
    width: 100%;
    font-size: 16px;
    line-height: 16px;
    padding: 5px;
  }
  button{
    font-size: 16px;
    color: #ffffff;
    line-height: 16px;
    padding: 10px;
    border: none;
    border-radius: 5px;
    background-color: orangered;
  }
  }
</style>



